import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import { Provider as ThemeProvider } from './context/theme'
import {Provider as LanguageProvider} from './context/language'


ReactDOM.render(
  <React.StrictMode>
    <ThemeProvider>
    <LanguageProvider>
    <App />
    </LanguageProvider>
    </ThemeProvider>
  </React.StrictMode>,
  document.getElementById('root')
);



